import './styles/style.css';
import greet from './src/greeting.js';

if (process.env.NODE_ENV !== 'production') {
    console.log('Looks like we are in development mode!');
} else {
    console.log('production time!');
}
let visible = 'about';
const navigation = document.getElementById('navigation');

navigation.addEventListener('click', (e) => {
    switchVisible(e.target.value);
});

function switchVisible(elementValue) {
    console.log(`visible ${visible}`);
    console.log(`elementValue ${elementValue}`);
    switch (elementValue) {
        case 0:
            document.getElementById(visible).style.visibility = 'hidden';
            document.getElementById('about').style.visibility = 'visible';
            visible = 'about';
            break;
        case 1:
            document.getElementById(visible).style.visibility = 'hidden';
            document.getElementById('skills').style.visibility = 'visible';
            visible = 'skills';
            break;
        case 2:
            document.getElementById(visible).style.visibility = 'hidden';
            document.getElementById('projects').style.visibility = 'visible';
            visible = 'projects';
            break;
        case 3:
            document.getElementById(visible).style.visibility = 'hidden';
            document.getElementById('contact').style.visibility = 'visible';
            visible = 'contact';
            break;
        default:
            console.log('something went wrong');
    }
}
